    font-family: $textparagraph;
    padding-left:15px;
    font-size: 14px;
    font-weight: 400;
    line-height: 19px;  
}
.flexalert1 p a{
    color: #6d6d6d;
    font-family: $textparagraph;
  
    font-size: 14px;
    font-weight: 400;
    line-height: 19px;  
    text-decoration:none;
}
.toglerino{
    color:#1cdbe7;
}
.dskaska{
    margin-top:4px;
}
// Media
@media screen and (max-width:1470px){
    .maoncontent__tourbooker_first_1{
        margin:10px auto 0px auto;
    }
    .maoncontent__tourbooker_second_2{
        margin:10px auto 0px auto;
        padding-bottom:50px;
    }
}
@media screen and(max-width:1140px){
    .maoncontent__suggest__compilation_div{
        background:none;
    }
    .maoncontent__suggest__compilation_div_2{
        margin:10px auto 0px auto;
    }
    .maoncontent__suggest__compilation_div_1{
        margin:10px auto 0px auto;
    }
    .maoncontent__destin_block_div{
        margin:10px auto 0px auto;
    }
}
.maoncontent__notyet div{
    width:221px;
    max-width:100%;
    height:auto;
    margin:102px auto 0px auto;
}
.maoncontent__notyet div img{
    display: block;
    max-width:100%;
    height:auto;
}
@media screen and(max-width:1114px){
    .maoncontent__notyet_prefooters_div_2{
        margin:10px auto 0px auto;
    }
}
@media screen and(max-width:927px){
    .maoncontent_aboutus_rightside{
        margin:10px auto 0px auto
    }
    .maoncontent_aboutus_leftside{
        margin:10px auto 0px auto;
    }
}

@media screen and(max-width:920px){
    
    .formlist__content_form{
        justify-content: flex-start;
    }
    .formlist__content_form_date{
       margin:10px auto 0px auto;
    }
    .formlist__content_form_date_2{
        margin:10px auto 0px auto;
    }
    .formlist__content_form_text{
        margin:10px auto 0px auto;
    }
    .formlist__content_form button{
        margin:10px auto 0px auto;
    }
    .jq-selectbox{
        margin:10px auto 0px auto;
    }
}
@media screen and (max-width:570px){
    .maoncontent__notyet button{
        display: block;
        margin:10px auto 0px auto;
    }
    .maoncontent__notyet input{
        border: 2px solid #e7e6e6;
        border-radius: 3px;
        margin:0 auto;
        display: block;
    }
}